import { Location } from '@angular/common';
import { Inject, Injectable, InjectionToken, Optional } from '@angular/core';
import { flatten, has, isUndefined } from 'lodash-es';
import { Subject } from 'rxjs';

import {
  IdReference,
  IManagedObject,
  InventoryService,
  IOperation,
  IOperationBulk,
  IResult,
  OperationBulkService,
  OperationService
} from '@c8y/client';

import { OperationDetails } from './operation-details.model';
import { OperationType } from './operation-type.model';
import { BulkOperationType } from './bulk-operation.model';

export const baseUrl = 'devicecontrol/bulk/creation/';
export const HOOK_LIST_BULK_TYPE = new InjectionToken<OperationType | OperationType[]>(
  'LIST_BULK_TYPE'
);

@Injectable()
export class BulkOperationsService {
  readonly DD_LOW_COUNT: number = 10;
  firmwareId: Subject<IdReference> = new Subject<IdReference>();

  private bulkTypes: OperationType[];

  constructor(
    private operationBulkService: OperationBulkService,
    private operationService: OperationService,
    private inventoryService: InventoryService,
    private location: Location,

    @Optional() @Inject(HOOK_LIST_BULK_TYPE) bulkTypes: Array<OperationType | OperationType[]>
  ) {
    this.bulkTypes = flatten(bulkTypes);

    this.bulkTypes = this.bulkTypes.map(type => {
      if (isUndefined(type.selected)) {
        type.selected = false;
      }
      return type;
    });
  }

  getBulkOperations(customFilter = {}) {
    const filter = {
      withTotalPages: true,
      withDeleted: true,
      pageSize: 50,
      ...customFilter
    };

    return this.operationBulkService.list(filter);
  }

  getBulkOperationById(bulkOperationId: string | number) {
    return this.operationBulkService.detail(bulkOperationId);
  }

  createBulkOperation(bulkOperation: Partial<IOperationBulk>) {
    return this.operationBulkService.create(bulkOperation);
  }

  deleteBulkOperation(bulkOperationId) {
    return this.operationBulkService.delete(bulkOperationId);
  }

  updateBulkOperation(bulkOperation: Partial<IOperationBulk>) {
    return this.operationBulkService.update(bulkOperation);
  }

  getOperation(id: string): Promise<IResult<IOperation>> {
    return this.operationService.detail(id);
  }

  returnToBulkOperationOverview() {
    this.location.back();
  }

  setBulkTypes(list: OperationType[]) {
    this.bulkTypes = list;
  }

  getBulkTypes(): OperationType[] {
    return this.bulkTypes;
  }

  setFirmwareId(id: IdReference) {
    this.firmwareId.next(id);
  }

  createGroup(deviceQueryDataString: string) {
    const dynamicGroup: Partial<IManagedObject> = {
      name: 'Bulk operations group',
      type: 'c8y_DynamicGroup',
      c8y_IsDynamicGroup: { invisible: {} },
      c8y_DeviceQueryString: deviceQueryDataString
    };

    return this.inventoryService.create(dynamicGroup);
  }

  async scheduleBulkOperation(deviceQueryString: string, details: OperationDetails) {
    const dynamicGroup = await this.createGroup(deviceQueryString);

    const bulkOperation: IOperationBulk = {
      groupId: dynamicGroup.data.id,
      operationPrototype: details.prototype,
      creationRamp: details.schedule.delayInSeconds,
      startDate: details.schedule.scheduledDate.toISOString(),
      note: details.note
    };

    await this.createBulkOperation(bulkOperation);
  }

  getSingleOperationsByStatus(status, bulkOperationId) {
    const filter = {
      withTotalPages: true,
      bulkOperationId,
      status: (status && status.toUpperCase()) || ''
    };

    return this.operationService.list(filter);
  }

  createSingleOperation(operation: IOperation) {
    return this.operationService.create(operation);
  }

  updateSingleOperation(partialUpdateObject: Partial<IOperation>) {
    return this.operationService.update(partialUpdateObject);
  }

  getManagedObject(deviceId: IdReference) {
    return this.inventoryService.detail(deviceId);
  }

  retrieveBulkOperationType(operation: IOperation): BulkOperationType {
    let type: BulkOperationType;

    this.bulkTypes.some(t => {
      if (t.fragments.some(fragment => has(operation, fragment))) {
        type = t.type;
        return true;
      }
    });

    return type;
  }
}
